@use 'base/variables' as v;
@use 'base/mixins' as m;

.destinations {

    &Grid {
        @include m.grid(1, 2rem);

        @include m.tablet {
            @include m.grid(2, 2rem);
        }

        @include m.desktop {
            @include m.grid(4, 2rem);
        }
    }
}

.destination {
    display: flex;
    justify-content: space-evenly;
    gap: 2rem;
    align-items: center;

    &Image{
        width: 14rem;
        height: 14rem;
    }

    &Name,
    &Distance {
        margin: 0;
    }
}